
#main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
#main > h1 {
  text-align: center;
  font-size: 120px;
  margin: 0;
  padding: 0;
  /* color: transparent; */
  /* -webkit-text-stroke: 2px #ffffff5d; */
  background: -webkit-linear-gradient(
    -86deg,
    #ffffff7d 0%,
    #ffffff2d 50%,
    #ffffff8d 100%
  );
  -webkit-background-clip: text;
  -webkit-text-stroke: 4px transparent;
  color: #202020;
}
#main p {
  color: white;
}
/*provvisorio*/
.main-element {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.temporary-controls {
  margin-top: 5px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.temporary-controls .btn {
  margin: 0px 5px;
}



